import {
  Box,
  Heading,
  Center,
  Input,
  Text,
  Button,
  NumberInput,
  NumberInputField,
} from "@chakra-ui/react";
import { useState } from "react";
import "./App.css";

function App() {
  const [numeroProva, setNumeroProva] = useState(0);
  const [valoreDaInserire, setValoreDaInserire] = useState("" as any);

  return (
    <Box>
      <Center>
        <Box top={10} w="50%" p={4}>
          <Heading>
            Ora inserirete prima il numero della vostra prova e poi la parola
            magica!
          </Heading>
        </Box>
      </Center>
      <Box top={10} w="50%" p={4} m="auto">
        <Text mb={4}>Inserire il numero della prova</Text>
        <NumberInput
          defaultValue={0}
          onChange={(valueString) => setNumeroProva(parseInt(valueString))}
          value={numeroProva}
          min={0}
        >
          <NumberInputField />
        </NumberInput>
      </Box>
      <Box top={10} w="50%" p={4} m="auto">
        <Text mb={4}> Inserire la parola magica</Text>
        <Input
          onChange={(valueString) => setValoreDaInserire(valueString)}
          value={valoreDaInserire}
          placeholder="Dario"
        ></Input>
      </Box>
      <Box top={10} w="50%" p={4} m="auto">
        <Button colorScheme="blue">Controlla i risultati!</Button>
      </Box>
    </Box>
  );
}

export default App;
